      background: url(../img/prettyPhoto/light_rounded/sprite.png) -1px -1px no-repeat;
    }
    .pp_details {
      position: relative;
    }
    .pp_description {
      margin-right: 85px;
    }
    #pp_full_res .pp_inline {
      color: #000;
    }
    .pp_gallery a {
      &.pp_arrow_previous,
      &.pp_arrow_next {
        margin-top: 12px !important;
      }
    }
    .pp_nav {
      .pp_play {
