 ------------------------------------------------------------------------- */

.pp_top {
  position: relative;

  height: 20px;
}

* html .pp_top {
  padding: 0 20px;
}

.pp_top {
  .pp_left {
    position: absolute;
    left: 0;

    width: 20px;
    height: 20px;
  }
  .pp_middle {
    position: absolute;
    right: 20px;
    left: 20px;

    height: 20px;
  }
}

* html .pp_top .pp_middle {
  position: static;
  left: 0;
}

.pp_top .pp_right {
  position: absolute;
  top: 0;
  right: 0;
  left: auto;

  width: 20px;
  height: 20px;
}

.pp_content {
  min-width: 40px;
  height: 40px;
}

* html .pp_content {
  width: 40px;
}

.pp_fade {
  display: none;
}

.pp_content_container {
  position: relative;

  width: 100%;

  text-align: left;
  .pp_left {
    padding-left: 20px;
  }
  .pp_right {
    padding-right: 20px;
  }
  .pp_details {
    float: left;

    margin: 10px 0 2px 0;
  }
}

.pp_description {
  display: none;

  margin: 0;
}

.pp_social {
  float: left;

  margin: 0;
  .facebook {
    float: left;
    overflow: hidden;

    width: 55px;
  }
  .twitter {
    float: left;

    margin-right: 5px;
  }
}

.pp_nav {
  float: left;
  clear: right;

  margin: 3px 10px 0 0;
  p {
    float: left;

    margin: 2px 4px;

    white-space: nowrap;
  }
  .pp_play,
  .pp_pause {
    float: left;

    margin-right: 4px;

    text-indent: -10000px;
  }
}

a {
  &.pp_arrow_previous,
  &.pp_arrow_next {
    display: block;
    float: left;
    overflow: hidden;

    width: 14px;
    height: 15px;
    margin-top: 3px;

    text-indent: -10000px;
  }
}

.pp_hoverContainer {
  position: absolute;
  z-index: 2000;
  top: 0;

  width: 100%;
}

.pp_gallery {
  position: absolute;
  z-index: 10000;
  left: 50%;

  display: none;

  margin-top: -50px;
  div {
    position: relative;

    float: left;
    overflow: hidden;
  }
  ul {
    position: relative;

    float: left;

    height: 35px;
    margin: 0 0 0 5px;
    padding: 0;

    white-space: nowrap;
    a {
      display: block;
      float: left;
      overflow: hidden;

      height: 33px;

      border: 1px #000 solid;
      border: 1px rgba(0, 0, 0, .5) solid;
      &:hover {
        border-color: #fff;
      }
    }
  }
  li.selected a {
    border-color: #fff;
  }
  ul a img {
    border: 0;
  }
  li {
    display: block;
    float: left;

    margin: 0 5px 0 0;
    padding: 0;
    &.default a {
      display: block;

      width: 50px;
      height: 33px;

      background: url(../img/prettyPhoto/facebook/default_thumbnail.gif) 0 0 no-repeat;
      img {
        display: none;
      }
    }
  }
  .pp_arrow_previous,
  .pp_arrow_next {
    margin-top: 7px !important;
  }
}

a {
  &.pp_next {
    display: block;
    float: right;

    width: 49%;
    height: 100%;

    text-indent: -10000px;

    background: url(../img/prettyPhoto/light_rounded/btnNext.png) 10000px 10000px no-repeat;
  }
  &.pp_previous {
    display: block;
    float: left;

    width: 49%;
    height: 100%;

    text-indent: -10000px;

    background: url(../img/prettyPhoto/light_rounded/btnNext.png) 10000px 10000px no-repeat;
  }
  &.pp_expand,
  &.pp_contract {
    position: absolute;
    z-index: 20000;
    top: 10px;
    right: 30px;

    display: none;

    width: 20px;
    height: 20px;

    cursor: pointer;
    text-indent: -10000px;
  }
  &.pp_close {
    line-height: 22px;

    position: absolute;
    top: 0;
    right: 0;

    display: block;

    text-indent: -10000px;
  }
}

.pp_bottom {
  position: relative;

  height: 20px;
}

* html .pp_bottom {
  padding: 0 20px;
}

.pp_bottom {
  .pp_left {
    position: absolute;
    left: 0;

    width: 20px;
    height: 20px;
  }
  .pp_middle {
    position: absolute;
    right: 20px;
    left: 20px;

    height: 20px;
  }
}

* html .pp_bottom .pp_middle {
  position: static;
  left: 0;
}

.pp_bottom .pp_right {
  position: absolute;
  top: 0;
  right: 0;
  left: auto;

  width: 20px;
  height: 20px;
}

.pp_loaderIcon {
  position: absolute;
  top: 50%;
  left: 50%;

  display: block;

  width: 24px;
  height: 24px;
  margin: -12px 0 0 -12px;
}

#pp_full_res {
  line-height: 1 !important;
  .pp_inline {
    text-align: left;
    p {
      margin: 0 0 15px 0;
    }
  }
}

div.ppt {
  font-size: 17px;

  z-index: 9999;

  display: none;

  margin: 0 0 5px 15px;

  color: #fff;
}
